import React, { Component } from "react";
import userData from "./api/userData";

class App extends Component {
  constructor() {
    super();
    this.state = {
      img: "https://via.placeholder.com/150",
      error: false,
      errorMsg: null
    };
  }
  handleSubmit = e => {
    e.preventDefault();
    userData(this.refs.username.value)
      .then(data => {
        console.log(data);
        this.setState({
          img: data.hd_profile_pic_url_info.url,
          error: false
        });
      })
      .catch(err => {
        console.log("from app.js", err);
        this.setState({ error: true, errorMsg: "User Not Found" });
      });
  };

  render() {
    return (
      <div className="container">
        <div className="flex">
          <div className="box">
            <h2>Instagram DP</h2>
            <div className="form-container">

            <form onSubmit={this.handleSubmit}>
              <input ref="username"  placeholder="Enter username"/>
              <button type="submit">Search</button>
            </form>
            </div>
            {this.state.error ? (
              <div>{this.state.errorMsg}</div>
            ) : (
              <a target={"_blank"} href={this.state.img}>
                <img alt={"instagram dp"} src={this.state.img} />
              </a>
            )}
          </div>

        </div>
      </div>
    );
  }
}

export default App;
